.faq-list-item {
    list-style-type: none;
    margin: 0 0 8px;
    font-size: 1.625rem;
}

.faq-list-item-question {
    background: #303030;
    display: block;
    text-align: left;
    border: 0;
    font-weight: 400;
    margin-bottom: 1px;
    padding: 0.8em 2.2em 0.8em 1.2em;
    position: relative;
    width: 100%;
}

.faq-list-item-question,
.faq-list-item-question button {
    color: inherit;
    font: inherit;
    margin: 0;
    cursor: pointer;
}

.faq-list-item-answer {
    background: #303030;
    font-weight: 400;
    text-align: left;
    transition: max-height .25s cubic-bezier(.5, 0, .1, 1);
    max-height: 0;
    overflow: hidden;
    margin-top: 1px;
}

.faq-list-item-answer.open {
    max-height: 1200px;
    margin-top: 1px;
}

.faq-list-item-answer-text {
    display: inline-block;
    padding: 1.2em;
}

.faq-list-item-question-svg {
    overflow: hidden;
    fill: #fff;
    height: 1em;
    position: absolute;
    right: 1em;
    top: 50%;
    width: 1em;
    transition: .3s smooth;
    transform: translate(0, -50%) rotate(-45deg);
}

.faq-list-item-question-svg.cross {
    transform: translate(-0.025em, -0.4em);
}




@media (max-width: 950px) {
    .faq-list-item {
        font-size: 1.125rem;
    }
}

@media (max-width: 550px) {
    .faq-list-item {
        font-size: 1rem;
    }
}